import React from 'react';
import Classnames from './menu.module.css';
// import inflag from '../../../assets/images/in.svg';
class Menu extends React.Component {

    render() {
        return (
            <div className={Classnames.rows}>
                <div className={Classnames.custom_dropdown}>
                    <select name="Category" id="Category"
                        value={this.props.selectedValue}
                        onChange={this.props.handleChange}>
                        <option value="business">business</option>
                        <option value="entertainment">entertainment</option>
                        <option value="general">general</option>
                        <option value="health">health</option>
                        <option value="science">science</option>
                        <option value="sports">sports</option>
                        <option value="technology">technology</option>
                    </select>
                </div>
                {/* <img className={Classnames.image} src={inflag} alt=" "/> */}
                <div className={Classnames.custom_dropdown}>
                    <select name="Country" id="Country"
                        value={this.props.selectedValueCountry}
                        onChange={this.props.handleChangeCountry}>
                        <option value="in">India</option>
                        <option value="us">us</option>
                    </select>
                </div>
            </div>
        )
    }
}

export default Menu;